import { Fragment, ReactNode, useEffect, useMemo } from "react"
import { useNavigate } from "react-router-dom"
import { useRequest } from "../../hook/useRequest"
import TextLoading from "../TextLoading"

/**
 * 保证子元素在登录后才渲染，未登录时则跳转到登陆页面
 */
const AssertLogin : React.FC<{children: ReactNode}>= ({children}) => {
  const isLoginRequester = useRequest('USER_ISLOGIN')
  const navi = useNavigate()
  const isLogin = () => isLoginRequester.result._tag === 'SUCCESS' && isLoginRequester.result.data

  isLoginRequester.useInitEffect({})
  
  useEffect(() => {
    switch (isLoginRequester.result._tag) {
      case 'PENDING': return
      case 'FATAL': 
        alert('请求服务器发生异常！')
        throw isLoginRequester.result.error
      default:
        if (!isLogin()) {
          navi('/signin')
        }
        break;
    }
  }, [isLoginRequester.result])
  return <Fragment>{isLogin() ? children : <TextLoading text="少女祈祷中" />}</Fragment>
}


export default AssertLogin